}

 .Oleo{
  font-family: 'Oleo Script Swash Caps', cursive;
}

.Montserrat{
 font-family: 'Montserrat', sans-serif;
}

.App {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
}

.nav-wrapper{
  width: 100vw;
  height: 100vh;

}

.nav-wrapper img{
  width: 2000px ;
  height: 1333px;
  position: absolute;
}

.images{
    width: 100vw;
    height: 100vh;
    position: relative;
    z-index: -1;
    overflow: hidden;
    margin-bottom: 0;
}
.images img{
 width: 2000px ;
  height: 1333px;
  top: 0px;
  position: absolute;
}

.image-wrapper{
    position: absolute;
    width: 100vw;
    height: 100vh;
    bottom: 0;
}

#esp{
  opacity: 0;

}

.box{
    width: 100vw;
  min-height: 100vh;
  margin: 0;
}

#p1{
  z-index: 1;
}

#p2{
  z-index: 3;
}

#p3{
  z-index: 2;
}

.title{
      width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
position: absolute;
color: white;
background-color: rgba(32,35,53,0.4);

}
#title{
  font-size: 7em;
  margin-bottom: 0;
}

#subtitle{
  font-size: 2em;
  margin-top: 0;

}

.b1{
   background-image: url('./pexels2.jpg');
}

.page1{
    width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  color: black;
  padding-top: 10vh;
 
}

.page1 p{
    max-width: 50vw;
}


.page2{
    width: 100vw;
  height: 130vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: black;
background-color: #8fa1bc;
}

.page2 li{
  padding-bottom: 2vh;
}

.page2a{
    width: 50vw;
  height: 130vh;
background-image: url('./motif1.jpg');
}

#page2b{
  max-width: 50vw;
  max-height: 130vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  padding-right: 10vw;
  padding-left: 5vw;
  overflow: hidden;
}

.btsuite{
  width: 30px;
  height: 30px;
  left :50vw;
  top: 80vh;
  background-color: white;
  transform: rotate(45deg) ;
  z-index: 1;
}


.btsuite2{
  width: 30px;
  height: 30px;

  background-color: #a9aeb4;
  transform: rotate(45deg) ;
  z-index: 1;
  margin-top: 25vh;
}





















.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
